html, body {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

.body {
  display: flex;
  flex-direction: column;
  position: relative;
}

.page__content {
  flex: 1 0 auto;
}
.page__footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1728px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

ul.dots li {
  display: flex;
  align-items: center;
}
ul.dots li:not(:last-of-type) {
  margin-bottom: 8px;
}
ul.dots li::before {
  content: " ";
  width: 5px;
  height: 5px;
  background-color: transparent;
  border-radius: 50%;
  border: 1px solid black;
  margin-right: 8px;
}

.header {
  margin: 20px 0px;
}
.header .container {
  width: 100%;
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  color: white;
  border-radius: 12px;
  padding: 8px 26px;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

.footer {
  margin: 20px 0px;
}
.footer .container {
  width: 100%;
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  color: white;
  border-radius: 12px;
  padding: 8px 26px;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .section-catalog .download-link {
    height: 101px;
  }
  .header__col3 {
    min-width: 312px;
  }
}
@media screen and (-ms-high-contrast: active) and (max-width: 1200px), screen and (-ms-high-contrast: none) and (max-width: 1200px) {
  .header__col3 {
    min-width: auto;
    max-width: 100%;
  }
}